/* ====================== Restaurant Filtering ====================== */

.filter-options {
  max-width: 100%;
  min-height: 50px;
  background-color: $blue-3;
  padding: 10px;

  h2 {
  	color: white;
  	font-size: 1rem;
  	font-weight: normal;
  	line-height: 1;
  	margin: 0 20px;
  }

  select {
  	background-color: white;
  	border: 1px solid $white-2;
  	font-family: $main-font-family;
  	font-size: 11pt;
  	height: 35px;
  	letter-spacing: 0;
  	margin: 10px;
  	padding: 0 10px;
  	width: 200px;
  }
}

/* ====================== Restaurant Listing ====================== */

#restaurants-list {
  background-color: $white-3;
  list-style: outside none none;
  margin: 0;
  padding: 30px 15px 60px;
  text-align: center;

  @media (min-width: $xs-s){
    display: grid;
    grid-template-columns: 1fr 1fr;

  }

  @media (min-width: $s-m){
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }

  @media (min-width: $m-l){
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-column-gutter: 0px;
  }

p {
	margin: 0;
	font-size: 11pt;
}

  li {
  	background-color: $white-2;
  	border: 2px solid $grey-4;
  	font-family: Arial,sans-serif;
  	margin: 15px;
  	padding: 25px 30px 25px;
  	text-align: left;
  	max-width: 100%;

  	text-align: center;

    justify-self: center;


    @media (min-width: $xs-s){
      margin: 15px 10px 15px;
    }
      
      .restaurant-container{

        display: grid;
        grid-template-columns: 5fr 1fr;
        grid-column-gap: 10px;

        text-align: left;

        h2{
         color: $orange-2;
         font-family: Arial,sans-serif;
         font-size: 14pt;
         font-weight: 200;
         letter-spacing: 0;
         line-height: 1.3;
         margin: 20px 0 10px;
         text-transform: uppercase;
        }

        .fav-button{
          display: inline-block;
          border: none;
          padding: 0;
          text-decoration: none;
          background-color: transparent;
          font-family:Arial sans-serif;
          font-size: 20pt;
          cursor: pointer;
          text-align: center;
          transition: background 250ms ease-in-out, 
                      transform 150ms ease;
          -webkit-appearance: none;
          -moz-appearance: none;
          
          &:hover, &:focus{
            color: $orange-2;
          }

        }

        p{
          grid-column-end: span 2;
        }

      }


  		a {
  			background-color: $orange-1;
		  	border-bottom: 3px solid $white-4;
		  	color: $white-2;
		  	display: inline-block;
		  	font-size: 10pt;
		  	margin: 15px 0 0;
		  	padding: 8px 30px 10px;
		  	text-align: center;
		  	text-decoration: none;
		  	text-transform: uppercase;
  		}

	}

	.restaurant-img {
  		background-color: $grey-4;
  		display: block;
  		max-width: 100%;
	}


}

.favorited {
  .fav-button{
    color: $orange-1;
  }
}